'use client';

import type { Transaction } from '@/types';
import { LocalStorage$Keys } from '../enums';
import { create } from 'zustand';
import { persist } from 'zustand/middleware';
import dayjs from '../util/dayjs';

type TransactionsStoreProps = {
    transactions: Transaction[];
    addTransaction: (transaction: Transaction) => void;
    removeTransaction: (transactionId: string) => void;
    removeBulkTransactions: (transactionIds: string[]) => void;
    updateTransaction: (transactionId: string, transactionUpdated: Partial<Transaction>) => void;
};

export const useTransactionsStore = create<TransactionsStoreProps>()(persist((set) => ({
    transactions: [],
    addTransaction: (transaction: Transaction) => set((state) => ({ transactions: [...state.transactions, transaction], })),
    removeTransaction: (transactionId: string) => set((state) => ({ transactions: state.transactions.filter((t) => t.id !== transactionId), })),
    updateTransaction: (transactionId: string, transactionUpdated: Partial<Transaction>) => set((state) => ({ transactions: state.transactions.map((t) => t.id === transactionId ? { ...t, ...transactionUpdated, updatedAt: dayjs().toISOString() } : t), })),
    removeBulkTransactions: (transactionIds: string[]) => set((state) => ({ transactions: state.transactions.filter((t) => !transactionIds.includes(t.id)), })),
}),
    {
        name: LocalStorage$Keys.TRANSACTIONS,
    }));
