

export type SavedAccount = {
    id: string;
    name: string;
    updatedAt: string;
    iconName: string;
    budget: number;
    transactions: string[]; //. Transactions IDs
};

export type Transaction$Flags = {
    isPaid: boolean;
};

export enum Transaction$Type {
    INCOME = 'income',
    EXPENSE = 'expense'
};

export type Transaction = {
    id: string;
    savedAccountId: string;
    updatedAt: string;
    iconName: string;
    name: string;
    amount: number;
    type: Transaction$Type;
    flags: Transaction$Flags;
};


export type Transaction$Summary = {
    totalIncome: number; // Ingresos totales (pagados + pendientes)
    totalExpense: number; // Gastos totales (pagados + pendientes)

    paidIncome: number; // Ingresos ya cobrados
    pendingIncome: number; // Ingresos an no cobrados

    paidExpense: number; // Gastos ya pagados
    pendingExpense: number; // Gastos an no pagados

    netBalance: number; // totalIncome - totalExpense
    availableBudget: number; // budget + paidIncome - paidExpense (lo que realmente puede gastar)

    budgetUsedPercent: number; // porcentaje de presupuesto usado
    budgetRemainingPercent: number; // porcentaje de presupuesto restante
    expenseCategories: Map<string, number>;
    incomeCategories: Map<string, number>;
};
