import { Transaction$Summary, SavedAccount, Transaction, Transaction$Type } from '@/types';
import { getCategoryByIconName } from '../functions';


export const getTransactionsSummary = (account: SavedAccount, transactions: Transaction[]): Transaction$Summary => {

    const budget = Number(account.budget) || 0;

    const summary: Transaction$Summary = {
        totalIncome: 0,
        totalExpense: 0,
        paidIncome: 0,
        pendingIncome: 0,
        paidExpense: 0,
        pendingExpense: 0,
        netBalance: 0,
        availableBudget: 0,
        budgetUsedPercent: 0,
        budgetRemainingPercent: 0,
        expenseCategories: new Map<string, number>(),
        incomeCategories: new Map<string, number>(),
    };

    let netBalance = 0;

    for (const transaction of transactions) {
        const amount = Number(transaction.amount) || 0;

        if (transaction.type === Transaction$Type.INCOME) {
            summary.totalIncome += amount;
            if (transaction.flags.isPaid) {
                summary.paidIncome += amount;
                netBalance += amount;
                const category = getCategoryByIconName(transaction.iconName);
                if (!category) continue;
                summary.incomeCategories.set(category.categoryName, (summary.incomeCategories.get(category.categoryName) || 0) + amount);
            } else summary.pendingIncome += amount;

        } else if (transaction.type === Transaction$Type.EXPENSE) {
            summary.totalExpense += amount;
            if (transaction.flags.isPaid) {
                summary.paidExpense += amount
                netBalance -= amount;
                const category = getCategoryByIconName(transaction.iconName);
                if (!category) continue;
                summary.expenseCategories.set(category.categoryName, (summary.expenseCategories.get(category.categoryName) || 0) + amount);
            } else summary.pendingExpense += amount;
        }
    };
    
    summary.netBalance = netBalance;
    summary.availableBudget = budget - summary.paidExpense;
    summary.budgetUsedPercent = budget > 0 ? (summary.totalExpense / budget) * 100 : 0;
    summary.budgetRemainingPercent = Math.max(0, 100 - summary.budgetUsedPercent);

    return summary;
};


