export const getSelectedAccount = () => {
  const savedAccountStore = useSavedAccountsStore.getState();
  const selectedAccountId = savedAccountStore.selectedAccountId;

  const savedAccounts = savedAccountStore.savedAccounts;
  return savedAccounts.find((account) => account.id === selectedAccountId) ?? null;
};

export const getTransactions = (savedAccountId: string) => {
  const transactionsStore = useTransactionsStore.getState();
  return transactionsStore.transactions.filter(transaction => transaction.savedAccountId === savedAccountId);
}

