export const getCategoriesSections = () => {
  const categories = getCategories();
  const options = Object.entries(categories.reduce((acc, c) => {
    if (!acc[c.categoryName]) acc[c.categoryName] = [];
    acc[c.categoryName].push(c);
    return acc;
  }, {} as Record<string, Category[]>)).flatMap(([categoryName, items]) => [
    {
      value: `header-${categoryName}`,
      label: categoryName,
      isHeader: true
    },
    ...items.map(item => ({
      value: item.id,
      icon: <DynamicIcon name={item.iconName as 'slice'} />,
      label: undefined
    }))
  ]);

  return options;
}
export const getCategoryById = (id: string) => {
  const categories = getCategories();
  return categories.find((category) => category.id === id);
};

export const getCategoryByIconName = (iconName: string) => {
  const categories = getCategories();
  return categories.find((category) => category.iconName === iconName);
};
