'use client';

import { create } from 'zustand';
import { LocalStorage$Keys } from '../enums';
import { persist } from 'zustand/middleware';

type PrintStyleStore = {
    printStyle: 'unified' | 'separated';
    setPrintStyle: (printStyle: 'unified' | 'separated') => void;
};

export const usePrintStyleStore = create<PrintStyleStore>()(persist<PrintStyleStore>((set) => ({
    printStyle: 'unified',
    setPrintStyle: (printStyle: 'unified' | 'separated') => set({ printStyle }),
}), {
    name: LocalStorage$Keys.PRINT_STYLE,
}));
