'use client';

import { SavedAccount } from '@/types';
import { create } from 'zustand';
import { persist } from 'zustand/middleware';
import { LocalStorage$Keys } from '../enums';
import dayjs from '../util/dayjs';

type SavedAccountStoreProps = {
    savedAccounts: SavedAccount[];
    selectedAccountId: string;
    addSavedAccount: (savedAccount: SavedAccount) => void;
    setSavedAccounts: (savedAccounts: SavedAccount[]) => void;
    removeSavedAccount: (savedAccountId: string) => void;
    updateSavedAccount: (savedAccountId: string, savedAccountUpdated: SavedAccount) => void;
    setSelectedAccountId: (savedAccountId: string) => void;
  
};

export const useSavedAccountsStore = create<SavedAccountStoreProps>()(persist((set) => ({
    savedAccounts: [],
    addSavedAccount: (savedAccount) => set((state) => ({ savedAccounts: [...state.savedAccounts, savedAccount], })),
    setSavedAccounts: (savedAccounts) => set({ savedAccounts }),
    removeSavedAccount: (savedAccountId) => set((state) => ({ savedAccounts: state.savedAccounts.filter((account) => account.id !== savedAccountId), })),
    updateSavedAccount: (savedAccountId, savedAccountUpdated) => set((state) => ({ savedAccounts: state.savedAccounts.map((account) => account.id === savedAccountId ? { ...savedAccountUpdated, updatedAt: dayjs().toISOString() } : account) })),
    setSelectedAccountId: (savedAccountId) => set({ selectedAccountId: savedAccountId }),
    selectedAccountId: '',
}), {
    name: LocalStorage$Keys.SAVED_ACCOUNTS
}));

